import React from 'react';
import { default as MuiAppBar, AppBarProps } from '@mui/material/AppBar';

const AppBar = React.forwardRef<HTMLElement, AppBarProps>( ( props, ref ) => {
	return (
		<MuiAppBar
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<AppBarProps>;

export default AppBar;
