import React from 'react';
import { default as MuiToolbar, ToolbarProps } from '@mui/material/Toolbar';

const Toolbar = React.forwardRef<HTMLElement, ToolbarProps>( ( props, ref ) => {
	return (
		<MuiToolbar
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ToolbarProps>;

export default Toolbar;
