import React, { FC } from 'react';
import clsx from 'clsx';
import AppBar, { AppBarProps } from '@elementor/ui/AppBar';
import Toolbar, { ToolbarProps } from '@elementor/ui/Toolbar';
import SvgIcon, { SvgIconProps } from '@elementor/ui/SvgIcon';
import Stack from '@elementor/ui/Stack';
import CloseButton from '@elementor/ui/CloseButton';
import { styled, useThemeProps, createSlots } from '@elementor/ui/styles';
import './DialogHeader.types';

export interface DialogHeaderProps extends AppBarProps {
	children?: React.ReactNode;
	className?: string;
	logo?: React.ReactNode | false;
	onClose?: ( event: React.SyntheticEvent ) => void;
	slotProps?: {
		logo?: SvgIconProps;
		toolbar?: ToolbarProps;
	},
}

interface LogoProps extends SvgIconProps {
	logo?: React.ReactNode | false;
}

const ElementorLogo: FC<SvgIconProps> = ( props ) => {
	return (
		<SvgIcon viewBox="0 0 32 32" { ...props }>
			<path fillRule="evenodd" clipRule="evenodd" d="M2.69648 24.8891C0.938383 22.2579 0 19.1645 0 16C0 11.7566 1.68571 7.68687 4.68629 4.68629C7.68687 1.68571 11.7566 0 16 0C19.1645 0 22.2579 0.938383 24.8891 2.69648C27.5203 4.45459 29.5711 6.95344 30.7821 9.87706C31.9931 12.8007 32.3099 16.0177 31.6926 19.1214C31.0752 22.2251 29.5514 25.0761 27.3137 27.3137C25.0761 29.5514 22.2251 31.0752 19.1214 31.6926C16.0177 32.3099 12.8007 31.9931 9.87706 30.7821C6.95344 29.5711 4.45459 27.5203 2.69648 24.8891ZM12.0006 9.33281H9.33437V22.6665H12.0006V9.33281ZM22.6657 9.33281H14.6669V11.9991H22.6657V9.33281ZM22.6657 14.6654H14.6669V17.3316H22.6657V14.6654ZM22.6657 20.0003H14.6669V22.6665H22.6657V20.0003Z" />
		</SvgIcon>
	);
};

const StyledElementorLogo = styled( ElementorLogo )( ( { theme } ) => ( {
	width: theme.spacing( 3 ),
	height: theme.spacing( 3 ),
	'& path': {
		fill: theme.palette.text.primary,
	},
	marginRight: theme.spacing( 1 ),
} ) );

const StyledCustomLogo = styled( 'span' )( ( { theme } ) => ( {
	marginRight: theme.spacing( 1 ),
} ) );

const Logo: FC<LogoProps> = ( { logo, ...rest } ) => {
	if ( logo === false ) {
		return null;
	}

	if ( logo ) {
		return <StyledCustomLogo>{ logo }</StyledCustomLogo>;
	}

	return <StyledElementorLogo { ...rest } />;
};

const { slots, classNames } = createSlots( 'DialogHeader', [ 'root', 'logo', 'toolbar' ] );

export type DialogHeaderSlots = keyof typeof slots;

const Root = styled( AppBar, slots.root )<{ ownerState: DialogHeaderProps }>( {
	'& .MuiDialogTitle-root': {
		padding: 0,
	},
} );

const ToolbarSlot = styled( Toolbar, slots.toolbar )<{ ownerState: DialogHeaderProps }>( {} );

const DialogHeader = React.forwardRef<HTMLDivElement, DialogHeaderProps>( ( inProps, ref ) => {
	const props = useThemeProps( { props: inProps, name: slots.root.name } );

	const { slotProps = {}, logo, onClose, ...rootProps } = props;

	return (
		<Root
			{ ...rootProps }
			ref={ ref }
			className={ clsx( [ [ classNames.root, rootProps.className ] ] ) }
			ownerState={ props }
		>
			<ToolbarSlot
				variant="dense"
				{ ...slotProps.toolbar }
				className={ clsx( [ classNames.toolbar, slotProps.toolbar?.className ] ) }
				ownerState={ props }
			>
				<Logo
					logo={ logo }
					className={ clsx( [ classNames.logo, slotProps.logo?.className ] ) }
				/>

				<Stack direction="row" alignItems="center" flex={ 1 }>
					{ props.children }
				</Stack>

				{
					onClose && (
						<CloseButton edge="end" onClick={ onClose } sx={ { '&.MuiButtonBase-root': { ml: 0.5 } } } />
					)
				}
			</ToolbarSlot>
		</Root>
	);
} ) as React.FC<DialogHeaderProps>;

DialogHeader.defaultProps = {
	color: 'transparent',
	position: 'relative',
};

export default DialogHeader;
