import React from 'react';
import { default as MuiStack, StackProps } from '@mui/material/Stack';

const Stack = React.forwardRef<HTMLElement, StackProps>( ( props, ref ) => {
	return (
		<MuiStack
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<StackProps>;

export default Stack;
